<template>
  <div class="app-container" id="gunFiles">
    <div class="filter-container">
      <el-input v-model="listQuery.iMei" placeholder="设备号/枪号" class="filter-item" @keyup.enter.native="handleFilter" />
      <el-button class="filter-item" type="primary" @click="handleFilter" icon="el-icon-search">搜索</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreate" v-if="power==1" icon="el-icon-circle-plus-outline">添加</el-button>
      <el-button class="filter-item" type="primary" @click="handleCreateAll" v-if="power==1" icon="iconfont iconpiliangtianjia">批量添加</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="exportExcel" icon="iconfont icondaochu" :loading="exportLoading">导出</el-button>
      <el-button class="filter-item filter-item-btn" type="primary" @click="uploadVisible=true" icon="iconfont icondaoru">导入</el-button>
    </div>
    <div class="tableBox">
      <el-table :key="tableKey" v-loading="listLoading" :data="list" fit highlight-current-row style="width: 100%;" :row-class-name="tableRowClassName">
        <el-table-column label="序号" fixed prop="id" sortable="custom" align="center" type="index" :index="indexMethod"></el-table-column>
        <el-table-column label="枪号" align="center">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.code }}</span>
            <span v-else>{{ row.code }}</span>
          </template>
        </el-table-column>
        <el-table-column label="设备号" align="center" v-if="power==1">
          <template slot-scope="{row}">
            <span v-if="power==1" class="link-type" @click="handleUpdate(row)">{{ row.iMei }}</span>
            <span v-else>{{ row.iMei }}</span>
          </template>
        </el-table-column>

        <el-table-column label="标签" align="center" v-if="power==1">
          <template slot-scope="{row}">
            <span>{{ row.rfid }}</span>
          </template>
        </el-table-column>
        <el-table-column label="使用人" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.securityName }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="登记日期" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.registrationDate }}</span>
        </template>
        </el-table-column> -->
        <!-- <el-table-column label="所属大队" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.teamName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="电量" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.battery }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="SIM卡号" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.gunNumber }}</span>
        </template>
        </el-table-column>-->
        <!-- <el-table-column label="出入库" width="120px" align="center">
          <template slot-scope="scope">
            <span>{{ formatRunStatus(scope.row.runType)}}</span>
          </template>
        </el-table-column> -->
        <el-table-column label="枪支类型" align="center" v-if="power==1">
          <template slot-scope="{row}">

            <span>{{ row.type==1?'普通枪':'机动枪' }}</span>
          </template>
        </el-table-column>
        <el-table-column label="状态" align="center">
          <template slot-scope="scope">
            <span>{{ formatStatus(scope.row.status) }}</span>
          </template>
        </el-table-column>
        <el-table-column label="枪支归还时间" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.returnTime }}</span>
          </template>
        </el-table-column>
        <!-- <el-table-column label="出入库" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.runType==1?'出库':'入库' }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="使用者" width="110px" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column label="购买日期" width="200px" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.buyDate }}</span>
        </template>
      </el-table-column>
      <el-table-column label="报废日期" width="200px" align="center">
        <template slot-scope="scope">
          <span>{{ scope.row.scrapDate }}</span>
        </template>
        </el-table-column>-->
        <!-- <el-table-column label="押运员名称" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.securityName }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column label="电池电量" width="200px" align="center">
          <template slot-scope="scope">
            <span>{{ scope.row.battery }}</span>
          </template>
        </el-table-column> -->
        <!-- <el-table-column
        label="暂停报警时间"
        align="center"
        min-width="120"
        fixed="right"
        class-name="small-padding fixed-width"
      >
        <template slot-scope="{row}">
          <span>{{ row.pauseTime==0?'小于1分钟':row.pauseTime}}</span>
        </template> 
        </el-table-column>-->
        <el-table-column label="操作" fixed="right" align="center" width="400" class-name="small-padding fixed-width">
          <template slot-scope="{row}">
            <el-button type="primary" size="mini" @click="handleBind(row)" icon="iconfont iconsuo" :disabled="(row.rfid!==null&&row.securityId!==null&&power==1)||(row.securityId!==null&&power!==1)">绑定</el-button>
            <!-- <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="row.securityId==-1&&row.teamId==-1" icon="iconfont iconsuo">解绑</el-button> -->
            <el-button type="warning" size="mini" @click="handleUnbind(row)" :disabled="(row.rfid==null&&row.securityId==null&&power==1)||(row.securityId==null&&power!==1)" icon="iconfont iconjiesuo5">解绑</el-button>

            <el-button type="primary" size="mini" @click="handleUpdate(row)" icon="el-icon-edit-outline">编辑</el-button>
            <el-button type="danger" size="mini" @click="deleteConfirmOpen(row)" :disabled="row.status==4" icon="iconfont iconbaofei">报废</el-button>
          </template>
        </el-table-column>
      </el-table>
    </div>
    <pagination v-show="total>0" :total="total" :page.sync="listQuery.page" :limit.sync="listQuery.limit" @pagination="getList" layout="prev, pager, next, sizes, total, jumper" />

    <el-dialog id="myDialog" :title="textMap[dialogStatus]" :visible.sync="dialogFormVisible" center>
      <el-form ref="dataForm" :rules="rules" :model="temp" label-position="left" class="formStyle">
        <el-form-item label="设备号" prop="imei" v-if="dialogStatus=='create'">
          <el-input v-model="temp.imei" />
        </el-form-item>

        <el-form-item label="枪号" prop="code">
          <el-input v-model="temp.code" :readonly="power!==1" />
        </el-form-item>
        <el-form-item label="所属大队" prop="gunId" v-if="dialogStatus=='update'&&power==1">
          <el-select v-model="temp.gunId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="使用状态" prop="status">
          <el-select v-model="temp.status" class="filter-item" placeholder="请选择">
            <el-option v-for="item in usageStatusOptions" :key="item.key" :label="item.display_name" :value="item.key" />
          </el-select>
        </el-form-item>
        <el-form-item label="枪支类型" prop="type">
          <el-select v-model="temp.type" class="filter-item" placeholder="请选择">
            <el-option v-for="item in typeOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <!-- <el-form-item label="押运员" prop="securityId" v-if="dialogStatus=='update'">
          <el-select v-model="temp.securityId" class="filter-item" placeholder="Please select">
            <el-option
              v-for="item in userOptions"
              :key="item.id"
              :label="item.name"
              :value="item.id"
            />
          </el-select>
        </el-form-item> -->
        <el-form-item label="电池电量" prop="battery">
          <el-input v-model.number="temp.battery" />
        </el-form-item>
        <el-form-item label="枪支归还时间" prop="returnTime">
          <el-time-picker v-model="temp.returnTime" placeholder="枪支归还时间" value-format="HH:mm:ss">
          </el-time-picker>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="dialogStatus==='create'?createData():updateData()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" @close="scrollTop" title="批量添加" :visible.sync="dialogVisible" center>
      <el-form class="addAllForm formStyle" ref="addAllForm" :rules="rules1" :model="addAlltemp" label-position="left">
        <!-- <el-form-item label="车牌号" prop="licenseNo">
          <el-input v-model="addAlltemp.licenseNo" />
        </el-form-item>-->
        <el-form-item label="设备号头" prop="head">
          <el-input v-model="addAlltemp.head" class="carNO" placeholder="(不包括后五位,例如“CX0512345”请输入“CX05”)" />
        </el-form-item>
        <!-- <el-form-item label="公司名称" prop="companyName" v-if="dialogStatus=='create'">
          <el-input v-model="temp.companyName" />
        </el-form-item>-->
        <el-form-item label="后五位开始" prop="begin">
          <el-input v-model="addAlltemp.begin" />
        </el-form-item>
        <el-form-item label="后五位结束" prop="end">
          <el-input v-model="addAlltemp.end" />
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="createAllCar()">确定</el-button>
      </div>
    </el-dialog>
    <el-dialog id="myDialog" :title="bindMap[bindDialogStatus]" :visible.sync="BindDialogFormVisible" center>
      <el-form ref="bindForm" :rules="bindRules" :model="bindTemp" label-position="left" class="formStyle">

        <el-form-item :label="`${bindMap[bindDialogStatus]}类型`" prop="type">
          <el-select filterable v-model="bindTemp.type" class="filter-item" placeholder="Please select" @change="selectChange">
            <el-option v-for="item in bindTypeOptions" :key="item.id" :label="item.name" :value="item.id" :disabled="item.disabled||(power!==1&&item.id==3)" />
          </el-select>
        </el-form-item>

        <el-form-item label="使用人" prop="personId" v-if="bindTemp.type==1&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.personId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in userOptions" :key="item.id" :label="item.name" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="大队" prop="teamId" v-if="bindTemp.type==2&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.teamId" class="filter-item" placeholder="Please select">
            <el-option v-for="item in largeTeamOptions" :key="item.id" :label="item.teamName" :value="item.id" />
          </el-select>
        </el-form-item>
        <el-form-item label="标签" prop="rfidId" v-if="bindTemp.type==3&&bindDialogStatus=='bind'">
          <el-select filterable v-model="bindTemp.rfidId " class="filter-item" placeholder="Please select">
            <el-option v-for="item in rfidOptions" :key="item.id" :label="item.rfid" :value="item.id" />
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="bindDialogStatus=='bind'?bindSubmit():unbindSubmit()">确定</el-button>
      </div>
    </el-dialog>
    <my-upload :visible.sync="uploadVisible" :uploadData="{type:3}" @dataUpdated="reqSuccess"></my-upload>

  </div>
</template>

<script>
import Pagination from "../../../components/Pagination"; // secondary package based on el-pagination
import myUpload from "@/components/myUpload"; // secondary package based on el-pagination

//枪支列表，添加枪支，编辑，删除，搜索，批量添加枪支
import {
  gunList,
  gunAdd,
  gunUpdate,
  gunDelete,
  gunSearch,
  gunAddBatch,
  bindPerson,
  unbindPerson,
  bindTeam,
  unbindTeam,
  gunBindRfid,
  gunUnbindRfid
} from "../../../api/managementArchives/gunFiles";
//大队下拉框数据，人员下拉框数据
import {
  teamsSelect,
  personsSelect,
  exportExcelReq
} from "../../../api/managementArchives/common";
import { queryR } from "../../../api/inOut/rfid";

const usageStatusOptions = [

  { key: 1, display_name: "使用中" },
  { key: 2, display_name: "维修" },
  { key: 3, display_name: "保养" },
  { key: 4, display_name: "报废" }
];
//批量添加时前五位和后五位的验证规则
const reasonableLen = (rule, value, callback) => {
  const reasonableLength = /[0-9]{5}/;
  if (!reasonableLength.test(value)) {
    callback(new Error("长度为5"));
  } else {
    callback();
  }
};
//未选择
const unSelect = (rule, value, callback) => {
  if (value == -1) {
    callback(new Error("请选择"));
  } else {
    callback();
  }
};
let myLoading = undefined; // 弹窗服务调用

export default {
  name: "gunFiles",

  components: { Pagination, myUpload },
  data() {
    return {
      power: JSON.parse(sessionStorage.getItem("userInfo")).Power, // 权限
      tableKey: 0,
      list: null, //枪支列表
      total: 0, //枪支总数
      indexMethod: 1, // 序号
      listLoading: false,
      listQuery: {
        page: 1,
        limit: 10,
        iMei: undefined
      },
      largeTeamOptions: [{ id: -1, teamName: "请选择" }], //大队下拉
      usageStatusOptions, //使用状态
      userOptions: [{ id: -1, name: "请选择" }], //人员下拉
      //添加修改搜索提交的数据
      temp: {
        imei: "",
        status: 1,
        battery: 0,
        code: "",
        type: 1,
        returnTime: ""

      },
      dialogFormVisible: false,
      //批量添加的数据
      addAlltemp: {
        head: undefined,
        begin: undefined,
        end: undefined
      },
      dialogStatus: "", //弹窗是添加或i是编辑状态
      textMap: {
        update: "编辑",
        create: "添加"
      },
      dialogVisible: false, //批量添加弹窗
      //添加编辑表单验证规则
      rules: {
        // code: [{ required: true, message: "请填写枪号", trigger: "blur" }],
        imei: [{ required: true, message: "请填写设备号", trigger: "blur" }],
        // battery: [
        //   { type: "number", message: "请填写电池电量", trigger: "blur" }
        // ],
        // status: [
        //   { required: true, message: "请选择使用状态", trigger: "change" }
        // ]
      },
      largeTeamOptions: [
        { id: -1, teamName: "请选择" }

      ], //大队下拉数据
      bindTypeOptions: [
        // { id: -1, name: "请选择" },
        { id: 1, name: "人员" },
        // { id: 2, name: "大队" },
        { id: 3, name: "标签" },


      ], //绑定类型下拉数据
      bindTemp: {
        gunId: -1,
        type: 3,
        personId: -1
      },
      BindDialogFormVisible: false,
      bindDialogStatus: "",//控制是绑定还是解绑窗口
      bindMap: {
        bind: "绑定",
        unbind: "解绑"
      },
      bindRules: {
        type: [
          { required: true, message: "请选择绑定类型", trigger: "change", validator: unSelect }
        ],
        personId: [
          { required: true, message: "请选择绑定人员", trigger: "change", validator: unSelect }
        ],
        teamId: [
          { required: true, message: "请选择绑定大队", trigger: "change", validator: unSelect }
        ],
        rfidId: [
          { required: true, message: "请选择标签", trigger: "change" }
        ],
      },
      bindobj: {},
      //批量添加表单验证规则
      rules1: {
        head: [
          {
            required: true,
            message: "请填写设备号头",
            trigger: "blur"
          }
        ],
        begin: [
          {
            required: true,
            message: "请填写设备号后五位区间头",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ],
        end: [
          {
            required: true,
            message: "请填写设备号后五位区间尾",
            trigger: "blur"
          },
          { validator: reasonableLen, trigger: "blur" }
        ]
      },
      pickerOptions: {
        disabledDate(time) {
          return time.getTime() > Date.now();
        },
        shortcuts: [
          {
            text: "今天",
            onClick(picker) {
              picker.$emit("pick", new Date());
            }
          },
          {
            text: "昨天",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24);
              picker.$emit("pick", date);
            }
          },
          {
            text: "一周前",
            onClick(picker) {
              const date = new Date();
              date.setTime(date.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", date);
            }
          }
        ]
      },
      pausetemp: {
        time: null
      },
      rules2: {
        time: [
          {
            required: true,
            message: "请填写暂停时间",
            trigger: "blur"
          }
        ]
      },
      dialogVisibleOfPause: false,
      rfidOptions: [],
      uploadVisible: false,
      typeOptions: [{ id: 1, name: '普通枪' }, { id: 2, name: '机动枪' }],
      exportLoading: false,

    };
  },
  created() {
    this.getList(); //初始化获取枪支列表
    this.getSelect(); //初始化弹窗的下拉框
  },
  methods: {
    deleteConfirmOpen(row) {
      this.$confirm('确定进行此操作吗?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        this.handleDelete(row)
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消'
        });
      });
    },
    reqSuccess: function (val) {  //val用于接收子组件传过来的值
      if (val == true) {
        this.getList();
      }
    },
    //运行情况
    formatRunStatus(status) {
      if (status == 0) {
        return "入库";
      } else if (status == 1) {
        return "出库";
      } else if (status == -1) {
        return ""
      }
    },
    getList() {
      this.$set(this.listQuery, 'page', this.listQuery.page)
      let _t = this;
      _t.listLoading = true;
      gunList({
        page: this.listQuery.page,
        limit: this.listQuery.limit,
        status: [1, 2, 3]
      })
        .then(res => {
          if (res.status == 0) {
            this.listQuery.page = this.listQuery.page
            this.listLoading = false;
            this.list = res.data;
            this.total = res.count;
            this.getSelect();


            this.indexMethod = (_t.listQuery.page - 1) * _t.listQuery.limit + 1;
          } else {
            this.$message.error(res.msg);
          }
        })
        .finally(() => {
          _t.listLoading = false;
        });
    },

    //点击搜素
    handleFilter() {
      if (this.listQuery.iMei == "" || this.listQuery.iMei == undefined) {
        this.listQuery.iMei = null;
        this.getList();
        return;
      }
      gunSearch({
        keyword: this.listQuery.iMei
      }).then(res => {
        if (res.status == 0) {
          this.listQuery.page = 1;
          this.listLoading = false;
          this.list = [];
          this.list = res.data;

          this.total = 0;
        } else {
          this.$message.error(res.msg);
        }
      });
    },
    //重置
    resetTemp() {
      this.temp = {
        imei: "",
        status: 1,
        battery: 0,
        code: "",
        returnTime: ""


      }
    },
    selectChange(val) {
      if (val == 1) {
        this.bindTemp.personId = this.bindobj.securityId;
      } else if (val == 2) {
        this.bindTemp.teamId = this.bindobj.teamId;
      }
    },
    handleBind(row) {
      this.getRfid(row);
      this.bindobj = { ...row };

      this.bindTypeOptions = [
        // { id: -1, name: "请选择" },
        { id: 1, name: "人员" },
        // { id: 2, name: "大队" },
        { id: 3, name: "标签" }

      ];
      this.bindDialogStatus = "bind";
      if (row.securityId !== null) {
        this.bindTypeOptions[0].disabled = true
      } else {
        this.bindTypeOptions[0].disabled = false

      }
      // if (row.teamId == -1) { this.bindTypeOptions[2].disabled = true }
      // if (row.rfidId === null) { this.bindTypeOptions[3].disabled = true }
      if (row.rfidId !== null) { this.bindTypeOptions[1].disabled = true } else {
        this.bindTypeOptions[1].disabled = false;
      }
      // this.bindTemp = {
      //   gunId: row.id,
      //   personId: row.securityId,
      //   type: 3
      // }
      this.bindTemp = {
        gunId: row.id,
        // rfidId: row.rfidId,
        rfidId: null,
        personId: null,


      }
      this.BindDialogFormVisible = true;
    },
    handleUnbind(row) {
      if (row.securityId == null) { this.bindTypeOptions[0].disabled = true } else {
        this.bindTypeOptions[0].disabled = false
      }
      // if (row.teamId == -1) { this.bindTypeOptions[2].disabled = true }
      // if (row.rfidId === null) { this.bindTypeOptions[3].disabled = true }
      if (row.rfidId === null) { this.bindTypeOptions[1].disabled = true } else {
        this.bindTypeOptions[1].disabled = false
      }

      this.bindDialogStatus = "unbind";
      this.bindTemp = {
        gunId: row.id,

      }
      this.BindDialogFormVisible = true;
    },
    bindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData;
          if (this.bindTemp.type == 1) {
            subData = {
              gunId: this.bindTemp.gunId,
              personId: this.bindTemp.personId,
            }
            bindPerson(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 2) {
            subData = {
              gunId: this.bindTemp.gunId,
              teamId: this.bindTemp.teamId,
            }
            bindTeam(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 3) {
            subData = {
              gunId: this.bindTemp.gunId,
              rfidId: this.bindTemp.rfidId,
            }
            gunBindRfid(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          }


        }
      })
    },
    unbindSubmit() {
      this.$refs["bindForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });

          let subData;
          if (this.bindTemp.type == 1) {
            subData = {
              gunId: this.bindTemp.gunId
            }
            unbindPerson(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 2) {
            subData = {
              gunId: this.bindTemp.gunId,
            }
            unbindTeam(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          } else if (this.bindTemp.type == 3) {
            subData = {
              gunId: this.bindTemp.gunId,
            }
            gunUnbindRfid(subData)
              .then(res => {
                if (res.status == 0) {
                  this.getList();
                  this.$message.success(res.msg);
                  this.BindDialogFormVisible = false;
                } else {
                  this.$message.error(res.msg);
                }
              })
              .finally(() => {
                myLoading.close();
              });
          }

        }
      })
    },
    //点击添加
    handleCreate() {
      this.resetTemp();
      this.dialogStatus = "create";
      this.dialogFormVisible = true;

      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //重置批量添加
    resetAddAllTemp() {
      this.addAlltemp = {
        head: undefined,
        begin: undefined,
        end: undefined
      };
    },
    //点击批量添加
    handleCreateAll() {
      this.resetAddAllTemp();
      this.dialogVisible = true;
      this.$nextTick(() => {
        this.$refs["addAllForm"].clearValidate();
      });
    },
    //批量添加
    createAllCar() {
      this.$refs["addAllForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          gunAddBatch(this.addAlltemp)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //添加
    createData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          // this.temp = this.changToNull(this.temp);
          let subData = { ...this.temp };
          if (subData.returnTime == undefined) {
            delete subData.returnTime
          }
          gunAdd(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //点击编辑
    handleUpdate(row) {
      this.temp = {
        id: row.id,
        status: row.status,
        battery: row.battery,
        code: row.code,
        type: row.type,
        returnTime: row.returnTime

      }
      this.dialogStatus = "update";
      this.dialogFormVisible = true;
      this.$nextTick(() => {
        this.$refs["dataForm"].clearValidate();
      });
    },
    //编辑
    updateData() {
      this.$refs["dataForm"].validate(valid => {
        if (valid) {
          myLoading = this.$loading({ target: "#myDialog .el-dialog" });
          let subData = { ...this.temp };
          if (subData.returnTime == undefined) {
            delete subData.returnTime

          }
          gunUpdate(subData)
            .then(res => {
              if (res.status == 0) {
                this.getList();
                this.$message.success(res.msg);
                this.dialogFormVisible = false;
                //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
                this.$store.commit("realTimeMonitor/ISEDITFILES", true);
              } else {
                this.$message.error(res.msg);
              }
            })
            .finally(() => {
              myLoading.close();
            });
        } else {
          return false;
        }
      });
    },
    //删除
    handleDelete(row) {
      let _t = this;
      _t.listLoading = true;
      gunDelete({
        gunId: row.id
      })
        .then(res => {
          if (res.status == 0) {
            if (_t.listQuery.page > 1 && _t.list.length <= 1) {
              // 最后一条返回上一页
              _t.listQuery.page -= 1;
            }
            this.getList();
            _t.$message.success("刪除成功");
            //实时监控检测到变化重新获取车辆和枪支车牌号或枪支号和imei号放入carAndGunObj，及时更新信息
            this.$store.commit("realTimeMonitor/ISEDITFILES", true);
          } else {
            this.$message.error(res.msg);
            _t.listLoading = false;
          }
        })
        .catch(err => {
          _t.listLoading = false;
        });
    },
    //显示状态
    formatStatus(status) {
      if (status == 1) {
        return "使用中";
      } else if (status == 2) {
        return "维修";
      } else if (status == 3) {
        return "保养";
      } else if (status == 4) {
        return "报废";
      } else if (status == 5) {
        return "未注册";
      }
    },
    //筛选出select选择的值和内容，参数分别代表（select的v-model值，option数组，option值，option内容，后台需要的id,后台需要的name）
    selectFilter(vId, selectList, id, name, id1, name1) {
      let obj = {};
      obj = selectList.find(item => {
        //这里的selectList就是上面遍历的数据源
        return item[id] === vId; //筛选出匹配数据
      });
      this.temp[name1] = obj[name];
    },
    //初始化下拉框
    getSelect() {
      var that = this;
      //大队
      teamsSelect({ status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.largeTeamOptions = res.data;
          that.largeTeamOptions.unshift({ id: -1, teamName: "请选择" });
        } else {
          if (res.data == null) {
            this.largeTeamOptions = [];
            this.largeTeamOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });
      //人员
      personsSelect({ work: 3, status: 1 }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.userOptions = res.data;
          that.userOptions.unshift({ id: -1, name: "请选择" });
        } else {
          if (res.data == null) {
            this.userOptions = [];
            this.userOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });

    },
    getRfid(row) {
      let that = this;
      this.rfidOptions = [];
      queryR({ rfidType: [80], status: [0] }).then(res => {
        if (res.status == 0) {
          if (res.data == null) {
            return;
          }
          that.rfidOptions = res.data;
          that.rfidOptions.unshift({ id: null, rfid: "请选择" });
        } else {
          if (res.data == null) {
            this.rfidOptions = [];
            this.rfidOptions.pop();
          }
          this.$message.error(res.msg);
        }
      });

    },
    //由于提交的时候如果对应的id为null,对应的name也必须要为null,在这里就统一处理
    changToNull(data) {
      if (data.gunId == null) {
        data.teamName = null;
      }
      if (data.securityId == null) {
        data.securityName = null;
      }
      return data;
    },
    scrollTop() {
      // 弹窗滚动到顶部
      let myDialog = document.querySelector("#myDialog .el-dialog__body");
      myDialog.scrollTop = 0;
    },
    tableRowClassName({ row, rowIndex }) {
      if (rowIndex % 2 == 0) {
        return "color-row";
      } else if (rowIndex % 2 == 1) {
        return "noColor-row";
      }

      return "";
    },
    exportExcel() {
      let _t = this;
      this.exportLoading = true;

      // 查找数据
      exportExcelReq({ excelType: 5, title: '枪支信息' }).then(res => {
        try {
          let enc = new TextDecoder('utf-8')
          let data = JSON.parse(enc.decode(new Uint8Array(res)))
          this.$message({
            message: data.msg,
            type: 'warning'
          });

        } catch (error) {
          let disposition = res.headers;
          let fileName = "枪支信息" + ".xls";
          let blob = new Blob([res], { type: "application/excel" }); // 假设文件为pdf
          let link = document.createElement("a");
          link.href = window.URL.createObjectURL(blob);
          link.download = fileName;
          link.click();
          link.remove();
        }
      }).finally(() => {
        this.exportLoading = false;
      });
    }
  }
};
</script>
