      font-size: 12px;
    }
  }

  .addAllForm {
    .el-form-item__label {
      width: 132px;
    }
    .el-form-item__content {
      margin-left: 132px;
    }
    .el-form-item__content {
      width: 293px;
    }
    .el-input {
      width: 288px;
    }
  }
  .formStyle {
    .el-input__prefix {
